@media screen and (max-width: 639px) {
    
    .collaboration {
        display: flex;
        flex-direction: column;
        width: 90%;
        margin-top: 20px;
        height: 10%;
        justify-content: space-around;
        h2 {
            color: #FEE8C0;
            font-family: "Workbench";
            font-size: 1.2em
        }
    .collaboration-img {
        display: flex;
        width: 100%;
        justify-content: space-around;
        align-items: center;
        img{
            width: 15px;
            height: 15px;
            object-fit: cover;
        }
        .all-logo {
            display: flex;
            width: 80%;
            overflow-x: scroll;
            justify-content: space-around;
        .logo{
            background: #FFFFFF;
           width: 40%;
           height: 30px;
           border-radius: 10px;
            img{    
                width: 100px;
                height: 50px;
                object-fit: contain;
            }
        }
        }
    }
}
}

@media screen and (min-width: 640px)  {
    .triangles {
        width: 0px;
        height: 0px;
        border-left: 15px solid transparent;
        border-right: 15px solid transparent;
        border-bottom: 15px solid #FEE8C0;
    }
    .n{
        rotate: 90deg;
    }
    .b{
        rotate: -90deg
    }
    .collaboration {
        margin-left: 10%;
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-top: 80px;
        height: 10%;
        justify-content: space-around;
        h2 {
            color: #FEE8C0;
            font-family: "Workbench";
            font-size: 2em;
            font-weight: 300;
        //    margin-left: 10%;
        }
    .collaboration-img {
       margin-top: 30px;
        height: 90%;
        display: flex;
        width: 100%;
        justify-content: space-around;
        align-items: center;
        img{
            width: 15px;
            height: 15px;
            object-fit: cover;
        }
        .all-logo {
            height: 100%;
            display: flex;
            width: 90%;
            overflow-x:hidden ;
            justify-content: space-around;
        .logo{
            background:#48576d;
          flex: 0 0 20vw;
           height: 100%;
           border-radius: 10px;
           margin-right: 10%;
           display: flex;
           align-items: center;
            img{    
                width: 100%;
                height: 90%;
                object-fit: contain;
                border-radius: 10%;
            }
        }
        }
    }
}
}
@media screen and (min-width: 1024px){
    .collaboration{
        h2{
            font-size: 2.5em !important;
        }
        width: 70% !important;
        .logo {
            flex: 0 0 10vw !important;
        }
    }

}