// tablette
@keyframes slideFromRight {
    0% {
     left: 100%;
    }
    100% {
      left: 0; /* Déplace l'image complètement à gauche */
    }
  }
@media screen and (min-width: 640px) and (max-width: 1023px) {
    // animation 
    .header-animation {
        animation: slideFromLeft 3s forwards; /* Durée de l'animation et direction */
    }
   
    .div01 {
        position: relative;
        
        z-index: 3;
        width: 26.955%;
        height: 100vh;
        flex-direction: column;
        justify-content: space-evenly;
        border-right: solid #DBBE6E 7px;
        
        .social-media-all{
            display: flex;
            img {
                width: 74.145%;
            }
        }
        .logo{
            width: 70%;
            object-fit: cover;
        }
        .languages{
            width: 100%;
            display: flex;
            justify-content: space-evenly;
            button{
                width: 38.255%;
                height: 31px;
            }
        }
        .navigation {
           
            height: 40vh;
            margin-bottom: 50px;
            ul{
                display: flex;
                flex-direction: column;
                justify-content: space-evenly;
                height: 100%;
                // font-size: 2em;
                li{
                    font-size: 1.5em;
                }
            }
            
        }
       
    }
}

//dekstop 
@media screen and (min-width: 1024px) {
    .header-animation {
        animation: slideFromLeft 3s forwards; /* Durée de l'animation et direction */
    }
    .div01{
        position: relative;
       
        width: 30%;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: flex-start;
        border-right: #DBBE6E solid 8px;
        z-index: 3;
        height: 100%;
        img{
            margin-left: 17.945%;
           
        }
       
        .languages {
            margin-left: 11.945%;
            width: 50%;
            display: flex;
            justify-content: space-between;
        }
        .navigation{
            margin-left: 6.945%;
            width: 90%;
            height: 50vh;
            ul {
                width: 100%;
                display: flex;
                flex-direction: column;
               left: 15%;
               height: 100%;
               justify-content: space-evenly;
               font-size: 2em;
            }
        }
        .social-media-all {
            height: 6.113%;
            margin-left: 11.945%;
            display: flex;
            width: 70%;
            justify-content: space-between;
            .social {
                height: 100%;
                width: 21.13%;
                a{
                    display: flex;
                    height: 100%;
                    width: 100%;
                    img{
                        height: 100%;
                        width: 100%;
                        // object-fit: cover;
                    }
                }
            }
        }
    }
}