@media screen and (max-width: 639px) {
    .about-me {
        height: 90%;
        display: flex;
        flex-direction: column;
        margin-top: 45px;
        .bloc-about-me {
            display: flex;
            flex-direction: column;
            height: 100%;
            justify-content: flex-end;
            align-items: center;

            img {
                height: 40vh;
                width: 85%;
                object-fit: cover;
                right: 0;
                position: relative;
               
            }
            .text-about-me {
                display: flex;
                // height: auto;
                flex-direction: column;
                align-items: center;
                overflow-y: scroll;
                height: auto; /* Hauteur auto pour permettre le défilement */
                position: relative;
                width: 91.25%;
                p {
                    font-family: "Kodchasan-regular";
                    margin-top: 20px;
                    text-align: center;
                }
            }
        }
    }
}

h1 {
    font-family: "Workbench";
    color: #fee8c0;
}

@media screen and (min-width: 640px) and (max-width: 1023px) {
    .about-me {
        .bloc-about-me {
            height: 100%;
            display: flex;
            flex-direction: column;
            align-items: center;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                animation: slideFromRight 2s ease-in ;
                position: relative;
            }
            .text-about-me {
                animation: slideFromLeft 2s ease-in ;
                display: flex;
                flex-direction: column;
                align-items: center;
                width: 95%;
                overflow-y: auto;
                margin-bottom: 15px;
                position: relative;
                h1 {
                    margin-bottom: 10px;
                    font-size: 2.2em;
                    font-weight: 300;
                }
                p {
                    font-family: "Kodchasan-bold";
                    text-align: center;
                    font-size: 1.2em;
                }
            }
        }
    }
}
@media screen and (min-width:1024px) {
.about-me {
    display: flex;
    .bloc-about-me{
        display: flex;
        flex-direction: row-reverse;
        padding: 35px;
        justify-content: space-around;
        align-items: center;
        img{
            position: relative;
            animation: slideFromTop 3s ease-in-out forwards; /* Durée de l'animation et direction */
            width: 100%;
            object-fit: cover;
            // height: 50%;
        }
        .text-about-me{
            animation: slideFromLeft3 3s ease-in-out forwards; /* Durée de l'animation et direction */
            // margin-top: 50px;
            left: 0;
            z-index: 3;
            position: absolute;
            height: 100vh;
           //padding: 35px;
            display: flex;
            flex-direction: column;
            width: 35%;
            overflow-y: auto;
            scrollbar-width: none; // Masque la barre de défilement pour Firefox
  -ms-overflow-style: none; // Masque la barre de défilement pour IE et Edge

  // Masque la barre de défilement pour WebKit (Chrome, Safari, etc.)
  &::-webkit-scrollbar {
    display: none;
  }
            h1{
                margin: 10%;
                font-size: 2.5em;
                font-weight: 200;
            }
            p{
                margin: 2% 0 2% 10%;
               // margin-top: 20px;
                font-size: 1.02em;
            }
        }
    }
}
}
