.ephemere{
  display: flex;
  opacity: 0;
  animation: sami 5s ease-in;
}
#root{
  opacity: 1;
  animation: sami2 5s ease-in;
}

@keyframes sami {
  0%{
    opacity:1
  }
  100%{
    opacity: 0;
  }
}
@keyframes sami2 {
  0%{
    opacity:0
  }
  100%{
    opacity: 1;
  }
}